import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {inject} from 'mobx-react';

import StudentForm from '../components/StudentForm';
import DataTable from '../components/DataTable';
import DateInput from '../components/DateInput';
import getCookie from '../utils/getCookie';

@inject('classesStore')
export default class Attendance extends Component {
  dateRef = React.createRef();
  state = { classes: null, class_id: '', stream_id: '', date: '' };
  dataTable = null;
  constructor(props) {
    super(props);
    const self = this;
    this.dataTable = React.createRef()
    this.dataTableConfig = {
      serverSide: true,
      processing: true,
      ajax: function(params, callback, settings) {
        params.class_id = self.state.class_id;
        params.stream_id = self.state.stream_id;
        params.date = self.state.date;
        axios.get('/api/attendance', {params: {params}}).then(response=>{
          callback(response.data);
        })
      },
      columns: [
        { data: 'admission_no', name: 'admission_no', 'class': 'text-center'},
        { data: 'firstname', name: 'firstname', 'class': 'text-center'},
        { data: 'lastname', name: 'lastname', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          let checked = data.date ? " checked='' " : ''
          return `<span>
              <div class="bootstrap-switch bootstrap-switch-wrapper bootstrap-switch-animate bootstrap-switch-${data.date ? 'on': 'off'}" style="width: 72px;"><div class="bootstrap-switch-container" style="width: 122px; margin-left: 0px;"><span class="bootstrap-switch-handle-on bootstrap-switch-info" style="width: 50px;"><i class="fa fa-check"></i></span><span class="bootstrap-switch-label" style="width: 30px;">&nbsp;</span><span class="bootstrap-switch-handle-off bootstrap-switch-info" style="width: 50px;"><i class="fa fa-times"></i></span><input type="checkbox" checked="" data-toggle="switch" data-on-color="info" data-off-color="info" data-on-text="<i class='fa fa-check'></i>" data-off-text="<i class='fa fa-times'></i>"></div></div>
            </span>`
        }}
      ],
    };
  }

  inputChanged = (event) => {
    let field = event.target.name;
    let val = event.target.value;
    if(field == 'stream_id')
      this.setState({stream_id: val}, this.filterChanged);
    else if(field == 'class_id')
      this.setState({class_id: val, stream_id: ''}, this.filterChanged);
    else
      this.setState({[field]: val}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.draw();
  }

	componentDidMount() {
    this.props.classesStore.list({},true).then(classes=>{
      this.setState({classes});
    })
	}

	render() {
		let match = this.props.match;

    let {classes} = this.state;
    let streams = null;

    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

		return <div className="container-fluid">
      <DataTable ref={this.dataTable} title="Attendance" config={this.dataTableConfig} toolbar={
        <>
          <select onChange={this.inputChanged} name="class_id" value={this.state.class_id} id='filterByClass' className="form-control col-4">
            <option value=''>-- Select Classes--</option>
            {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
          </select>
          <select onChange={this.inputChanged} name="stream_id" value={this.state.stream_id} id='filterByStream' className="form-control col-4">
            <option value=''>-- Select Stream--</option>
            {streams && streams.map( (stream) => <option key={stream.id} value={stream.id}>{stream.name}</option> )}
          </select>
          <div className="col-4">
            <DateInput onChange={this.inputChanged} name="date" className="form-control" placeholder="Enter Date" />
          </div>
        </>
      }>
        <th>Admission No</th>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Attendance</th>
      </DataTable>
		</div>

	}
}
