import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

export default class ReportForm extends Component {
  defaults = {title: '', message: '', date: ''};
  state = { data: {...this.defaults}, error: {}, loading: false }

  inputChanged(field,event) {
    let val = event.target.value;
    this.setState( (state) => (state.data[field] = val, state));
  }

  componentDidUpdate(prevProps,prevState) {
    if(this.props.edit){
      if(!lodash.isEqual(this.props.data, prevProps.data)){
        this.setState({data: {...this.defaults,...this.props.data}})
      }
    }
    if(!lodash.isEqual(this.props.error,prevProps.error)){
      this.setState({error: {...this.state.error,...this.props.error}})
    }
  }

  submit = () => {
    let data = this.state.data;
    data['student_id'] = parseInt(this.props.studentId) || this.props.studentId;
    this.setState({loading: true});
    return this.props.submit(data).then( res => {
      if(this.props.edit) {
        ShowNotification("Report Successfully Edited", "success", "nc-check-2");
      }else {
        ShowNotification("Report Created Successfully", "success", "nc-check-2");
      }
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    }).finally(_=>this.setState({loading:false}));

  }

  saveContinue = () => {
    this.submit().then(()=>{
      this.setState({data: {...this.defaults},error: {}});
    })
  }

  save = () => {
    this.submit().then(()=>this.props.close());
  }

  apply = () => {
    this.submit().then(()=>{
      this.setState({error: {}});
    })
  }

  delete = () => {
    confirmAction( () => {
      return this.props.delete().then(res=>{
        this.props.close();
        return {success: 'Deleted!', succes_text: 'Report was deleted'};
      }, error => {
        throw {error: "Report couldn't be deleted."};
      })
    }, {loading_title: 'Deleting...', loading_text: 'Deleting Report', canceled: "Deleting Report Operation Cancelled", consequence: 'Report will be deleted'});
  }

  cancel = () => {
    let cancelAsync = async () => this.props.cancel()
    if(lodash.isEmpty(this.props.data) || (!this.props.data.pending && lodash.isEqual(this.props.data, this.state.data))) {
      this.props.close();
      return;
    }
    confirmAction( () => {
      return cancelAsync().then(res=>{
        this.props.close();
        return {success: 'Cancelled!', succes_text: 'Student Report entry was cancelled'};
      }, error => {
        throw {error: "Student Report entry couldn't be removed."};
      })
    }, {loading_title: 'Cancelling...', loading_text: 'Cancelling entry', canceled: "", consequence: 'Entered information will be discarded'});
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.submitAction();
  }

  chooseAction = (e) => {
    switch(e.target.name) {
      case 'apply':
        this.submitAction = this.apply;
        break;
      case 'save':
        this.submitAction = this.save;
        break;
      case 'save_continue':
        this.submitAction = this.saveContinue;
        break;
    }
  }

  render() {
    let error = this.state.error || {};
    let titleInvalid='', messageInvalid='', dateInvalid='';
    if(error.errors) {
      titleInvalid = error.errors.title ? 'is-invalid' : '';
      messageInvalid = error.errors.message ? 'is-invalid' : '';
      dateInvalid = error.errors.date ? 'is-invalid' : '';
    }

    return <div className="container-fluid">
      {this.props.edit && <div className="row">
          <div className="col-md-12">
            <span className="pull-right">
              <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
            </span>
          </div>
      </div>}
      <div className="row">
        <div className="col-md-12">
          <div className="card stacked-form">
            <div className="card-header ">
              <h4 className="card-title">Report</h4>
            </div>
            <div className="card-body ">
              <form onSubmit={this.handleSubmit} >
                {error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
                <div className="form-group">
                  <label>Title</label>
                  <input value={this.state.data.title} onChange={this.inputChanged.bind(this,'title')} type="text" placeholder="Title" className={`form-control ${titleInvalid}`} required />
                  {titleInvalid && error.errors.title.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
                  <p className="help-block help-block-error"></p>
                </div>

                <div className="form-group">
                  <label>Message</label>
                  <textarea value={this.state.data.message} onChange={this.inputChanged.bind(this,'message')} type="text" placeholder="Enter message" className={`form-control ${messageInvalid}`} required ></textarea>
                  {messageInvalid && error.errors.message.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
                  <p className="help-block help-block-error"></p>
                </div>

                <div className="form-group col-md-6">
                  <label>Date</label>
                  <DateInput value={this.state.data.date} onChange={this.inputChanged.bind(this,'date')} type="text" placeholder="Date" className={`form-control ${dateInvalid}`} required />
                  {dateInvalid && error.errors.date.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
                  <p className="help-block help-block-error"></p>
                </div>

                <div className="card-footer ">
                  <div className="row">
                    <div className="col-sm-2">
                      <input onClick={this.chooseAction} type="submit" name="save" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
                    </div>
                    {this.props.edit ? <div className="col-sm-2">
                        <input onClick={this.chooseAction} type="submit" name="apply" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
                      </div> :
                      <div className="col-sm-3">
                        <input onClick={this.chooseAction} name="save_continue" type="submit" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
                      </div>
                    }
                    &nbsp;
                    <div className="col-sm-2">
                      <input onClick={this.cancel} type="button" value="CANCEL" disabled={this.state.loading} className="btn btn-warning" />
                    </div>
                  </div>
                </div>

              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  }
}
