import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { inject, observer } from 'mobx-react';
import lodash from 'lodash'

import StudentForm from '../components/StudentForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('studentStore')
@inject('classesStore')
@observer
export default class Student extends Component {
    constructor(props) {
        super(props);
        let locationState = this.props.location.state || {};
        this.state = {class_id: locationState.class_id, stream_name: locationState.stream_name};
        window.studentListActionEdit = this.edit;
        this.dataTable = React.createRef();
        var self = this;
        this.dataTableConfig = {
            serverSide: false,
            processing: false,
            ajax : function (params, callback, settings) {
                self.props.studentStore.list({class_id: self.state.class_id}, true, true).then(students=>{
                    callback({data: students});
                    return students;
                })
            },
            columns: [
                { data: 'admission_no', name: 'admission_no', 'class': 'text-center'},
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'stream_name', name: 'stream_name', 'class': 'text-center', render: (data,type,row) =>  data ? data : self.streamName(row.stream_id, row.class_id) },
                { data: 'class_name', name: 'class_name', 'class': 'text-center', render: (data,type,row) => data ? data : self.className(row.class_id) },
                { data: 'doa', name: 'doa', 'class': 'text-center', render: (data,type,row) => data ? (new Date(data)).toDateString() : '-' },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="studentListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    componentWillUnmount() {
        $('tr[title]').tooltip('dispose');
    }

    streamName = (streamId, classId) => {
        let streamName = '-';
        let selectedClass = this.state.classes && this.state.classes.find( (cls) => cls.id == classId );
        if(selectedClass) {
            let stream = selectedClass.streams && selectedClass.streams.find( (str) => str.id == streamId );
            streamName = stream ? stream.name : '-';
        }
        return streamName;
    }

    className = (classId) => {
        let selectedClass = this.state.classes && this.state.classes.find( (cls) => cls.id == classId );
        return selectedClass ? selectedClass.name : '-';
    }

    componentDidMount() {
        this.props.classesStore.list({}, true).then(classes=>{
          this.setState({classes});
        });
    }

    edit = (student) => {
        if(String(student).startsWith('dummy_')){
            this.props.history.push(`${this.props.match.url}/new?student=${student}`)
        } else {
            this.props.history.push(`${this.props.match.url}/${student}`);
        }
    }

    inputChanged = (event) => {
        let field = event.target.name;
        let val = event.target.value;
        if(field == 'stream_name'){
          this.setState({stream_name: val}, this.filterStream);
        }
        else if(field == 'class_id'){
          this.setState({class_id: val, stream_name: ''}, this.filterClass);
        }
    }

    filterClass = () => {
        this.dataTable.current.table.column(3).search('');
        this.dataTable.current.table.ajax.reload();
    }

    filterStream = () => {
        this.dataTable.current.table.column(3).search(this.state.stream_name).draw();
    }

    filterString = (event) => {
        let value = event.target.value;
        this.dataTable.current.table.search(value).draw();
    }

	render() {
        let match = this.props.match;
        let {classes} = this.state;
        let streams = null;

        if(classes) {
          let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
          if(selectedClass) {
            streams = selectedClass.streams;
          }
        }
		return <div className="container-fluid">
            <div className="row">
                <div className="col-md-12">
                  <span className="pull-right">
                    <Link to={`${match.url}/new`} className="btn btn-success details" href="/school/students/create">New Student</Link>
                  </span>
                </div>
              </div>
    	    <DataTable ref={this.dataTable} title="Students" config={this.dataTableConfig} softDeleteField="dod" toolbar={
            <>
              <select onChange={this.inputChanged} name="class_id" value={this.state.class_id} id='filterByClass' className="form-control col-4">
                <option value=''>-- Select Classes--</option>
                {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
              </select>
              <select onChange={this.inputChanged} name="stream_name" value={this.state.stream_name} id='filterByStream' className="form-control col-4">
                <option value=''>-- Select Stream--</option>
                {streams && streams.map( (stream) => <option key={stream.id} value={stream.name}>{stream.name}</option> )}
              </select>
              <div className="form-group col-4">
                <input type="text" className="form-control" onChange={this.filterString} placeholder="Search" />
              </div>
            </>
            }>
                <th>Admission NO</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>Stream</th>
                <th>Class</th>
                <th>Date of Adm</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

@inject('studentStore')
@observer
export class NewStudent extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
        let studentIdMatch = this.props.location.search.match(/student=([^&]+)/);
        if(studentIdMatch) {
            let studentId = studentIdMatch[1];
            this.props.studentStore.get(studentId, true).then(student => {
                if(student){
                    this.setState({data: student, error: student.__meta_error});
                }
            })
        }
    }
    submit = (data) => {
        return this.props.studentStore.create(data).then( student => {
            this.setState({error: {}, data: student})
            return student;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    cancel = () => {
        let studentIdMatch = this.props.location.search.match(/student=([^&]+)/);
        if(studentIdMatch) {
            let studentId = studentIdMatch[1];
            return this.props.studentStore.cancel(studentId);
        }
    }

    close = () => {
        this.props.history.push('/school/students', {class_id: this.state.data.class_id, stream_name: this.state.data.stream_name});
    }

    render() {
        return <StudentForm data={this.state.data} error={this.state.error} submit={this.submit} cancel={this.cancel} close={this.close} />
    }
}


@inject('studentStore')
@observer
export class EditStudent extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.student){
        this.setState({data: this.props.location.state.student})
    }else if(this.props.match.params.id){
        this.props.studentStore.get(this.props.match.params.id, true).then(student => {
            this.setState({data: student, error: (student.__meta_error || {}) });
        },error=>this.setState({error}))
    }
    
  }

  submit = (data) => {
        return this.props.studentStore.update(data).then( student => {
            this.setState({error: {}, data: student});
            return student;
        }, error => {
            this.setState({error});
            return Promise.reject(error);
        })
    }

    delete = () => {
        return this.props.studentStore.delete(this.props.match.params.id);
    }

    discontinue = () => {
        return axios.post(`/api/students/discontinue/${this.state.data.id}`).then(res=> {
            this.props.studentStore.saveItem(res.data);
            this.setState({error: {}, data: res.data});
            return res.data;
        }, error => {
            error = error.response ? error.response.data : error;
            this.setState({error});
            throw error;
        })
    }
    restore = () => {
        return axios.post(`/api/students/restore/${this.state.data.id}`).then(res=> {
            this.props.studentStore.saveItem(res.data);
            this.setState({error: {}, data: res.data});
            return res.data;
        }, error => {
            error = error.response ? error.response.data : error;
            this.setState({error});
            throw error;
        })
    }

    cancel = () => {
        return this.props.studentStore.cancel(this.props.match.params.id);
    }

    close = () => {
        this.props.history.push('/school/students', {class_id: this.state.data.class_id, stream_name: this.state.data.stream_name});
    }

    render() {
        return <StudentForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} delete={this.delete} discontinue={this.discontinue} restore={this.restore} cancel={this.cancel} close={this.close} />
    }
}