import React, { Component } from 'react';
import { Route } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import Student, {NewStudent, EditStudent} from './Student';

import Report, {StudentReports, NewReport, EditReport} from './Report';

import Attendance from './Attendance';

export default class StudentPages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
						<Route exact path={`${match.path}`} render={(props) => <Student {...props} /> } />
						<Route path={`${match.path}/new`} render={(props) => <NewStudent {...props} />} />
						<Route exact path={`${match.path}/report`} render={(props) => <Report {...props} />} />
						<Route exact path={`${match.path}/report/:id`} render={(props) => <StudentReports {...props} />} />
						<Route path={`${match.path}/report/:id/new`} render={(props) => <NewReport {...props} />} />
						<Route path={`${match.path}/report/:id/:reportId`} render={(props) => <EditReport {...props} />} />
						<Route path={`${match.path}/attendance`} render={(props) => <Attendance {...props} />} />
						<Route path={`${match.path}/:id`} render={(props) => <EditStudent {...props} />} />
						<Route path=""><h1>404 Not Found</h1></Route>
					</AnimatedSwitch>
	}
}