import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

@inject('classesStore')
export default class StudentForm extends Component {
	defaults = {
		firstname: '',
		lastname: '',
		admission_no: '',
		class_id: '',
		stream_id: '',
		fathers_name: '',
		fathers_contact: '',
		mothers_name: '',
		mothers_contact: '',
		dob: '',
		doa: '',
		gender: '',
		entry_marks: ''
	};

	state = {previewUrl: '', error: {}, loading: false, classes: null, loading: false, data: {...this.defaults}
	};

	componentDidMount() {
    this.props.classesStore.list(true).then(classes=>{
    	this.setState({classes});
    });
	}

	componentDidUpdate(prevProps,prevState) {
		if(!lodash.isEqual(this.props.data, prevProps.data)){
			this.setState({data: {...this.defaults,...this.props.data}})
		}
		if((this.props.data||{}).profile != (prevProps.data||{}).profile){
			let url = '/uploads/students_profile/' + this.props.data.profile;
			if(this.props.data.profile.startsWith('data:image/')){
				url = this.props.data.profile;
			}
			this.setState({previewUrl: url});
		}

		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: this.props.error})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		/// parseInt class_id and stream_id into stream for consistency in indexDB
		if(field == 'class_id' || field == 'stream_id'){
			val = parseInt(val) || val;
		}
		this.setState( (state) => state.data[field] = val);
	}

	profileChanged = (event) => {
		let files = event.target.files;
		if(files && files[0]) {
			var reader = new FileReader();
			reader.onload = (e) => {
				this.setState({previewUrl: e.target.result});
				this.setState( (state) => (state.data['profile'] = e.target.result, state));
			};
			reader.readAsDataURL(files[0]);
		}
	}

	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Student Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Student Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error});
			throw error;
		}).finally(_=>this.setState({loading:false}));
	}

	discontinue = () => {
		confirmAction( () => {
			return this.props.discontinue().then(res=>{
				this.props.close();
				return {success: 'Discontinued!', succes_text: 'Student was discontinued'};
			}, error => {
				throw {error: "Student couldn't be discontinued"};
			})
		}, {loading_title: 'Discontinuing...', loading_text: 'Discontinuing Member', canceled: "Discontinuing Student Operation Cancelled", consequence: 'Student information will be unaccessible in the next period'});
	}

	restore = () => {
		confirmAction( () => {
      return this.props.restore().then(res=>{
        this.props.close();
        return {success: 'Restored!', succes_text: 'Student was restored'};
      }, error => {
        throw {error: 'Student could not be restored.'};
      })
    }, {loading_title: 'Restoring...', loading_text: 'Restoring Student', canceled: "Restoring Student Operation Cancelled", consequence: 'Student information will be made accessible again'});
	}

	delete = () => {
		confirmAction( () => {
			return this.props.delete().then(res=>{
				this.props.close();
				return {success: 'Deleted!', succes_text: 'Student was deleted'};
			}, error => {
				throw {error: "Student couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Student', canceled: "Deleting Student Operation Cancelled", consequence: 'Student will be completely removed'});
	}

	cancel = () => {
		let cancelAsync = async () => this.props.cancel();
		if(lodash.isEmpty(this.props.data) || (!this.props.data.__meta_pending && lodash.isEqual(this.props.data, this.state.data))) {
			this.props.close();
			return;
		}
		confirmAction( () => {
			return cancelAsync().then(res=>{
				this.props.close();
				return {success: 'Cancelled!', succes_text: 'Student entry was cancelled'};
			}, error => {
				throw {error: "Student entry couldn't be removed."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Cancelling entry', canceled: "", consequence: 'Entered information will be discarded'});
	}

	handleSubmit = (e) => {
		e.preventDefault();
		this.submitAction();
	}

	chooseAction = (e) => {
		switch(e.target.name) {
			case 'apply':
				this.submitAction = this.apply;
				break;
			case 'save':
				this.submitAction = this.save;
				break;
			case 'save_continue':
				this.submitAction = this.saveContinue;
				break;
		}
	}

	render() {
		let { classes } = this.state;
		let error = this.state.error || {};
		let firstnameInvalid='', lastnameInvalid='', admissionNoInvalid='', genderInvalid='', entryMarksInvalid='', fathersNameInvalid='', fathersContactInvalid='',
			mothersNameInvalid='', mothersContactInvalid='', classInvalid='', streamInvalid='', dobInvalid='', doaInvalid='';
		if(error.errors) {
			firstnameInvalid = error.errors.firstname ? 'is-invalid' : '';
			lastnameInvalid = error.errors.lastname ? 'is-invalid' : '';
			admissionNoInvalid = error.errors.admission_no ? 'is-invalid' : '';
			genderInvalid = error.errors.gender ? 'is-invalid' : '';
			entryMarksInvalid = error.errors.entry_marks ? 'is-invalid' : '';
			fathersNameInvalid = error.errors.fathers_name ? 'is-invalid' : '';
			fathersContactInvalid = error.errors.fathers_contact ? 'is-invalid' : '';
			mothersNameInvalid = error.errors.mothers_name ? 'is-invalid' : '';
			mothersContactInvalid = error.errors.mothers_contact ? 'is-invalid' : '';
			classInvalid = error.errors.class_id ? 'is-invalid' : '';
			streamInvalid = error.errors.stream_id ? 'is-invalid' : '';
			dobInvalid = error.errors.dob ? 'is-invalid' : '';
			doaInvalid = error.errors.doa ? 'is-invalid' : '';
		}

		let streams = null;

		if(classes) {
			let selectedClass = classes.find( (cls) => cls.id == this.state.data.class_id );
			if(selectedClass) {
				streams = selectedClass.streams;
			}
		}

		return <div className="container-fluid">
			{this.props.edit && <div className="row">
    	    <div className="col-md-12">
    	      <span className="pull-right">
    	      	{this.state.data.dod ? <button type="button" className="btn btn-secondary" onClick={this.restore} >Restore</button>
    	      	 : <button type="button" className="btn btn-warning" onClick={this.discontinue} >Discontinue</button> }
    	      	&nbsp;
    	        <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
    	      </span>
    	    </div>
	    </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Student Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#" onSubmit={this.handleSubmit} >
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
					    		<div className="col-md-9">
					    				<div className="form-group">
								        <label>First Name</label>
								        <input value={this.state.data.firstname} onChange={this.inputChanged.bind(this,'firstname')} type="text" placeholder="First Name" className={`form-control ${firstnameInvalid}`} required />
								        {firstnameInvalid && error.errors.firstname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Last Name</label>
								        <input value={this.state.data.lastname} onChange={this.inputChanged.bind(this,'lastname')} type="text" placeholder="Last Name" className={`form-control ${lastnameInvalid}`} required />
								        {lastnameInvalid && error.errors.lastname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Admission No</label>
								        <input value={this.state.data.admission_no} onChange={this.inputChanged.bind(this,'admission_no')} type="text" placeholder="Admission No" className={`form-control ${admissionNoInvalid}`} required />
								        {admissionNoInvalid && error.errors.admission_no.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>
					    		</div>
						      <div className="col-md-3 d-flex align-items-center">
						      	<div>
						      	
						      	<div className="input-group">
						      		<div>
							      		<img src={this.state.previewUrl || '/img/profile-placeholder.jpg'} className="rounded img-fluid" style={{width: '100%', height: '100%'}} alt="Responsive image" />
							      	</div>
										  <span style={{position: 'absolute', bottom: 0}}>
										  	<span className="btn btn-primary"><i className="fa fa-camera"></i>Change</span>
										    <input type="file" onChange={this.profileChanged} style={{position: 'absolute', bottom: 0, top: 0, right: 0, left: 0}} className="custom-file-input" id="inputGroupFile01" aria-describedby="inputGroupFileAddon01" />
										  </span>
										</div>
										</div>
						      </div>
					      </div>

					      <div className="row">
					      	
					      	<div className="form-group col-md-6">
						        <label>Entry Marks(percentage)</label>
						        <input value={this.state.data.entry_marks} onChange={this.inputChanged.bind(this,'entry_marks')} type="text" placeholder="Entry marks" className={`form-control ${entryMarksInvalid}`} required />
						        {entryMarksInvalid && error.errors.entry_marks.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="form-group col-md-6">
						        <label>Gender</label>
						        <select value={this.state.data.gender} onChange={this.inputChanged.bind(this,'gender')} name="gender" className={`form-control ${genderInvalid}`} required >
						        	<option value="">Select Gender</option>
                      <option value="M">Male</option>
                      <option value="F">Female</option>
                    </select>
						        {genderInvalid && error.errors.gender.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

					      <div className="row">
						      <div className="form-group col-md-8">
						        <label>Father's Names</label>
						        <input value={this.state.data.fathers_name} onChange={this.inputChanged.bind(this,'fathers_name')} type="text" placeholder="Father's Names" className={`form-control ${fathersNameInvalid}`} required />
						        {fathersNameInvalid && error.errors.fathers_name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-4">
						        <label>Father's Contact</label>
						        <input value={this.state.data.fathers_contact} onChange={this.inputChanged.bind(this,'fathers_contact')} type="text" placeholder="Father's Contact" className={`form-control ${fathersContactInvalid}`} required />
						        {fathersContactInvalid && error.errors.fathers_contact.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>

					      <div className="row">
						      <div className="form-group col-md-8">
						        <label>Mother's Names</label>
						        <input value={this.state.data.mothers_name} onChange={this.inputChanged.bind(this,'mothers_name')} type="text" placeholder="Mother's Names" className={`form-control ${mothersNameInvalid}`} required />
						        {mothersNameInvalid && error.errors.mothers_name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-4">
						        <label>Mother's Contact</label>
						        <input value={this.state.data.mothers_contact} onChange={this.inputChanged.bind(this,'mothers_contact')} type="text" placeholder="Mother's Contact" className={`form-control ${mothersContactInvalid}`} required />
						        {mothersContactInvalid && error.errors.mothers_contact.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>

					      <div className="row">
						      <div className="form-group col-md-6">
						        <label>Class</label>
						        <select disabled={!classes} value={this.state.data.class_id} onChange={this.inputChanged.bind(this,'class_id')} name="class" className={`form-control ${classInvalid}`} required >
						        	<option value="">Select Class</option>
                      {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
                    </select>
						        {classInvalid && error.errors.class_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="form-group col-md-6">
						        <label>Stream</label>
						        <select disabled={!streams} value={this.state.data.stream_id} onChange={this.inputChanged.bind(this,'stream_id')} name="stream" className={`form-control ${streamInvalid}`} required >
						        	<option value="">Select Stream</option>
                      {streams && streams.map( (stream) => <option key={stream.id} value={stream.id}>{stream.name}</option> )}
                    </select>
						        {streamInvalid && error.errors.stream_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

					      </div>

					      <div className="row">
						      <div className="form-group col-md-6">
						        <label>Date of Birth</label>
						        <DateInput value={this.state.data.dob} onChange={this.inputChanged.bind(this,'dob')} type="text" placeholder="Date of Birth" className={`form-control ${dobInvalid}`} required />
						        {doaInvalid && error.errors.doa.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Date of Admission</label>
						        <DateInput value={this.state.data.doa} onChange={this.inputChanged.bind(this,'doa')} type="text" placeholder="Date of Admission" className={`form-control ${doaInvalid}`} required />
						        {doaInvalid && error.errors.doa.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="row">
						    	<div className="col-sm-2">
				            <input onClick={this.chooseAction} type="submit" name="save" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
				          </div>
							  	{this.props.edit ? <div className="col-sm-2">
				            	<input onClick={this.chooseAction} type="submit" name="apply" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
				          	</div> :
				          	<div className="col-sm-3">
				            	<input onClick={this.chooseAction} type="submit" name="save_continue" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
				          	</div>
				          }
				          <div className="col-sm-3">
				            	<input onClick={this.cancel} type="button" value="CANCEL" disabled={this.state.loading} className="btn btn-danger" />
				          </div>
						    </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  </div>

					</div>
		    </div>
		  </div>
		</div>

	}
}