import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { inject, observer } from 'mobx-react';
import lodash from 'lodash';

import ReportForm from '../components/ReportForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('studentStore')
@inject('classesStore')
@observer
export default class StudentList extends Component {
  constructor(props) {
    super(props);
    window.studentListActionShowReport = this.showReport;
    var self = this;
    this.state = {class_id: '', stream_name: ''};
    this.dataTable = React.createRef();
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax : function (params, callback, settings) {
          self.props.studentStore.list({class_id: self.state.class_id}, true).then(students=>{
              callback({data: students});
          })
      },
      columns: [
        { data: 'admission_no', name: 'admission_no', 'class': 'text-center'},
        { data: 'firstname', name: 'firstname', 'class': 'text-center'},
        { data: 'lastname', name: 'lastname', 'class': 'text-center'},
        { data: 'stream_name', name: 'stream', 'class': 'text-center'},
        { data: 'class_name', name: 'class', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
              <a onclick="studentListActionShowReport('${data.id}')" class="btn btn-link btn-warning details"><i class="nc-icon nc-bullet-list-67 icon-bold"></i></a>
            </span>`
        }}
      ],
      createdRow: function (row, data, index) {
        if(data.dod) {
          row.classList.add('table-danger');
        }
      }
    };
  }

  componentDidMount() {
      this.props.classesStore.list(true).then(classes=>{
        this.setState({classes});
      });
  }

  inputChanged = (event) => {
    let field = event.target.name;
    let val = event.target.value;
    if(field == 'stream_name'){
      this.setState({stream_name: val}, this.filterStream);
    }
    else if(field == 'class_id'){
      this.setState({class_id: val, stream_name: ''}, this.filterClass);
    }
  }

  filterClass = () => {
    this.dataTable.current.table.column(3).search('');
    this.dataTable.current.table.ajax.reload();
  }

  filterStream = () => {
      this.dataTable.current.table.column(3).search(this.state.stream_name).draw();
  }

  filterString = (event) => {
    let value = event.target.value;
    this.dataTable.current.table.search(value).draw();
  }

  showReport = (student) => {
    this.props.history.push(`${this.props.match.url}/${student}`);
  }

	render() {
		let match = this.props.match;
    let {classes} = this.state;
    let streams = null;

    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

		return <div className="container-fluid">
		  <div className="row">
		    <div className="col-md-12">
		      <span className="pull-right">
		      </span>
		    </div>
		  </div>
      <DataTable ref={this.dataTable} title="Students Report" config={this.dataTableConfig} toolbar={
        <>
          <select onChange={this.inputChanged} name="class_id" value={this.state.class_id} id='filterByClass' className="form-control col-4">
            <option value=''>-- Select Classes--</option>
            {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
          </select>
          <select onChange={this.inputChanged} name="stream_name" value={this.state.stream_name} id='filterByStream' className="form-control col-4">
            <option value=''>-- Select Stream--</option>
            {streams && streams.map( (stream) => <option key={stream.id} value={stream.name}>{stream.name}</option> )}
          </select>
          <div className="form-group col-4">
            <input type="text" className="form-control" onChange={this.filterString} placeholder="Search" />
          </div>
        </>
        }>
        <th>Admission NO</th>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Stream</th>
        <th>Class</th>
        <th className="disabled-sorting text-center noExport">Show Report</th>
      </DataTable>
		</div>

	}
}


