@inject('reportStore')
@observer
export class NewReport extends Component {
    state = { error: {} };

    componentDidMount() {
        let reportIdMatch = this.props.location.search.match(/report=([^&]+)/);
        if(reportIdMatch) {
            let reportId = reportIdMatch[1];
            this.props.reportStore.get(reportId, true).then(report => {
                if(report){
                    this.setState({data: report, error: report.error});
                }
            })
        }
    }

    submit = (data) => {
      return this.props.reportStore.create(data).then( report => {
          this.setState({error: {}, data: report})
          return report;
      }, error => {
          this.setState({error});
          return Promise.reject(error);
      });
    }

    cancel = () => {
        let reportIdMatch = this.props.location.search.match(/report=([^&]+)/);
        if(reportIdMatch) {
            let reportId = reportIdMatch[1];
            return this.props.reportStore.cancel(reportId);
        }
    }

    close = () => {
        this.props.history.push(`/school/students/report/${this.props.match.params.id}`);
    }
    render() {
      let  studentId = this.props.match.params.id;
      return <ReportForm studentId={studentId} error={this.state.error} submit={this.submit} close={this.close} cancel={this.cancel} />
    }
}

@inject('reportStore')
@observer
export class EditReport extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
    if(this.props.location.state && this.props.location.state.report){
      this.setState({data: this.props.location.state.report})
    }else if(this.props.match.params.id){
      this.props.reportStore.get(this.props.match.params.reportId, true).then(report => {
        this.setState({data: report});
      },error=>this.setState({error}))
    }
    
  }

  submit = (data) => {
    return this.props.reportStore.update(data).then( report => {
      this.setState({error: {}, data: report})
      return report;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  delete = () => {
    return this.props.reportStore.delete(this.props.match.params.reportId);
  }

  close = () => {
      this.props.history.push(`/school/students/report/${this.props.match.params.id}`);
  }

  cancel = () => {
      return this.props.reportStore.cancel(this.props.match.params.reportId);
  }

  render() {
    let  studentId = this.props.match.params.id;
    return <ReportForm studentId={studentId} edit={true} error={this.state.error} data={this.state.data} submit={this.submit} delete={this.delete} close={this.close} cancel={this.cancel} />
  }
}